const letters = "abcdefghijklmnopqrstuvwxyz";
export class RandomSectionModel {
    constructor(id) {
        this.name = "New Section";
        this.length = 5;
        this.useNumbers = true;
        this.useLetters = true;
        this.id = id;
    }
    clone() {
        let copy = new RandomSectionModel(this.id);
        copy.name = this.name;
        copy.length = this.length;
        this.useNumbers = this.useNumbers;
        this.useLetters = this.useLetters;
        return copy;
    }
    toString() {
        let choices = [];
        let output = [];
        if (this.useLetters) {
            for (let i = 0; i < letters.length; i++) {
                choices.push(letters[i]);
            }
        }
        if (this.useNumbers) {
            for (let i = 0; i < 9; i++) {
                choices.push(i.toString());
            }
        }
        if (choices.length === 0) {
            throw new Error("Cannot create random string from empty choices");
        }
        for (let i = 0; i < this.length; i++) {
            let index = Math.floor(Math.random() * choices.length);
            output.push(choices[index]);
        }
        return output.join("");
    }
}
export class RandomConfiguration {
    constructor() {
        this.sections = [];
        this.sections.push(new RandomSectionModel(Date.now()));
    }
    toString() {
        return this.sections.map((section) => section.toString()).join("-");
    }
}
